//---------------------------------------------------------//
// Filters
//---------------------------------------------------------// 

.filter {
	display: flex;
	margin-top: -5px;

	h1, h2, h3, h4, h5, h6, p {
		&:last-child {
			margin-bottom: 0;
		}
	}
	.filter-left, .filter-right {
		align-self: center;
		margin-bottom: $spacer-2;
	}
	.filter-right {
		margin-left: auto;
		text-align: right;
	}
}