//---------------------------------------------------------//
// Icons
//---------------------------------------------------------// 
// - All Icons
//---------------------------------------------------------//

.icon-image, .icon {
	display: inline-block;
	font-style: normal;
	font-weight: normal;
	line-height: 1;
	vertical-align: middle;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon {
	font-family: 'Fabric External MDL2 Assets';
}


.fa-linkedin {
	color: #2f74a6;
}

.icon-svg {
	path {
		fill: currentColor;
	}
}


// Icon Sizes

.icon-sm {
	font-size: rem(14);
}
.icon-md {
	font-size: rem(16);
}
.icon-lg {
	font-size: rem(18);
}


.icon-btn {
	&:hover, &:focus {
		color: $primary;
	}
}

// Icon Margin

.icon-margin-right {
	margin-right: rem(12);
}
.icon-margin-left {
	margin-left: rem(12);
}


.icon-image-settings {
	background: url('#{$icon-url}/icon-settings.png') 0 0 no-repeat;
	@include background-retina(4, 14);
}
.icon-image-arrow-updown {
	background: url('#{$icon-url}/icon-arrow-up-down.png') 0 0 no-repeat;
	@include background-retina(7, 10);
	position: relative;
	top: -2px;
}