//---------------------------------------------------------//
// Main
//---------------------------------------------------------// 



.main {
	flex-grow: 1;
	display: flex;
}

.content {
	flex-grow: 1;
	display: flex;
	.main-content {
		flex-grow: 1;
		padding: 20px;
		position: relative;
	}
	.right-content {
		flex-shrink: 0;
		margin-right: 10px;
		width: 310px;
		position: relative;
		padding-top: 20px;
		padding-bottom: 20px;
		.content-inner {
			padding: 0;
			position: relative;
			height: 100%;
		}
		.alert {
			position: absolute;
			top: 100%;
			width: 100%;
			z-index: 10;
			margin-top: 5px;
		}
	}
}

.column-content {
	position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.content-inner {
	position: relative;
	padding: 20px;
}





// Content header

.content-header {
	display: flex;
	//margin-top: -5px;
	h1, h2, h3, h4, h5, h6, p {
		&:last-child {
			margin-bottom: 0;
		}
	}
	.ch-left, .ch-right {
		align-self: center;
		margin-bottom: $spacer-3;
	}
	.ch-right {
		margin-left: auto;
		text-align: right;
	}
}


// Sidebar

.sidebar {
	width: 175px;
	border-right: 1px solid $border-color;
	background-color: $white;
	position: relative;
	flex-shrink: 0;
}






// Content Box

.content-box {
	border: 1px solid $border-color;
	margin-bottom: 5px;
	position: relative;
	&:last-child {
		margin-bottom: 0;
	}
	.nav-tabs {
		margin-bottom: 0;
	}
	.content-box-header {
		border-bottom: 1px solid $border-color;
		min-height: 30px;
		h1, h2, h3, h4, h5, h6 {
			border-right: 1px solid $border-color;
			margin-bottom: -1px;
			background-color: $white;
			display: inline-block;
			@extend .btn;
			padding-left: 15px;
			padding-right: 15px;
			border-radius: 0;
			box-shadow: none !important;
		}
		.nav-tabs {
			border-bottom: none;
		}
	}
	.content-box-inner {
		background-color: $white;
		padding: 15px;
	}
	.content-box-bottom {
		border-top: 1px solid $border-color;
		background-color: $gray-white;
		padding: 5px;
		font-size: 11px;
	}
	.table-wrapper {
		display: flex;
		flex-direction: column;
		width: 100%;
		overflow: hidden;
		background-color: $white;
	}
	.table {
		margin-bottom: 0;
	}
	.tab-pane {
		height: 100%;
		position: relative;
	}
	.table-header {
		padding: 20px 20px 0 20px;
		position: relative;
		z-index: 2;
	}
	.table-body {
		padding: 0 20px 20px 20px;
		background-color: $white;
	}
	.tab-content {

	}

}