//---------------------------------------------------------//
// Header
//---------------------------------------------------------// 


.header {
	flex-shrink: 0;
	position: relative;
}



// Navbar

.navbar {
	min-height: rem(42);
	padding: 0 15px 0 10px;
	border-bottom: 1px solid $border-color;
	box-shadow: 0 8px 18px rgba($black, 0.06);
	background-color: $white;
	display: flex;
	align-items: center;
	.navbar-brand {
		background: url('#{$img-url}/willow-logo.png') 0 0 no-repeat;
		@include background-retina(96, 31);
		display: block;
	}
	.navbar-left, .navbar-center, .navbar-right {
		align-self: center;
		flex: 1;
	}
	.navbar-right {
		text-align: right;
		display: flex;
		justify-content: flex-end;
	}
	.navbar-center {
		text-align: center;
	}
	.account-info {
		@extend .nav;
		margin-bottom: 0;
		margin-left: -6px;
		margin-right: -6px;
		li {
			display: inline-block;
			vertical-align: middle;
			margin-left: 6px;
			margin-right: 6px;
		}
		a {
			display: block;
		}
	}
}



// Notification

.notification {
	position: relative;
	display: inline-block;
	line-height: 1;
	.badge {
		width: rem(13);
		height: rem(13);
		line-height: rem(10);
		font-size: rem(8);
		position: absolute;
		top: rem(-5);
		right: rem(-5);
		border: 1px solid $white;
	}
	.fas, .fa {
		font-size: 16px;
		position: relative;
		top: 1px;
	}
	.dropdown-menu {
		width: 220px;
	}
	&:hover, &:focus {
		.icon-image {
			opacity: 0.6;
		}
	}
}