//---------------------------------------------------------//
// Avatar Details
//---------------------------------------------------------// 


.avatar {
	text-align: center;
	position: relative;
	width: 20px;
	height: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: $primary;
	border-radius: 50%;
	&.no-image {
		border: 1px solid $gray-300;
		font-size: 12px;
		font-weight: bold;
		line-height: 30px;
		background-color: $gray-300;
	}
	img, &.no-image {
		display: block;
		border-radius: 50%;
		max-height: 20px;
	}
	&.avatar-icon {
		line-height: rem(32);
		text-align: center;
		img {
			max-width: 100%;
			width: auto;
			height: auto;
			display: inline-block;
			border-radius: 0;
		}
	}
}

.name-id {
	white-space: nowrap;
}

.avatar-details {
	display: flex;
	word-break: break-all;
	font-size: rem(11);
	.avatar + .details {
		padding-left: 10px;
	}
	.avatar {
		flex-shrink: 0;
	}
	.avatar, .details {
		align-self: center;
		text-align: left;
		white-space: normal;
	}
	&.dropdown-toggle {
		&:after {
			align-self: center;
		}
	}
	h1, h2, h3, h4, h5, h6, p, ul, ol {
		&:last-child {
			margin-bottom: 0;
		}
	}
}

