//---------------------------------------------------------//
// Dropdown
//---------------------------------------------------------// 


.dropdown-toggle {
	&:after {
		content: '';
		border: none;
		background: url('#{$icon-url}/icon-dropdown-arrow.png') 0 0 no-repeat;
		@include background-retina(8, 5);
		margin-left: rem(8);
		position: relative;
		top: rem(1);
	}
	&.dropdown-block {
		display: flex;
		&:after {
			margin-left: auto;
		}
	}
	&:hover, &:focus {
		text-decoration: none;
	}
	&.no-arrow {
		&:after {
			display: none;
		}
	}
}



.dropdown-menu {
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.09);
	font-size: rem(12);
}


