//---------------------------------------------------------//
// Rich Text Editor
//---------------------------------------------------------// 

.rte-module {
	height: 100%;
	textarea.form-control {
		background: none;
		padding: 0;
		margin: 0;
		border: none;
		resize: none;
		box-shadow: none !important;
		width: 100%;
		height: 100%;
	}
}
