//---------------------------------------------------------//
// Badges
//---------------------------------------------------------// 

.badge {
	border: 1px solid transparent;
	color: $primary;
	font-size: 12px;
	display: inline-block;
	font-weight: 600;
	text-transform: uppercase;
	line-height: 1;
	font-style: normal;
	&.badge-rounded {
		width: 22px;
		height: 22px;
		border-radius: 50%;
		line-height: 22px;
		text-align: center;
		&.badge-sm {
			width: 16px;
			height: 16px;
			line-height: 16px;
			font-size: rem(7);
			position: relative;
			top: rem(-1);
		}
	}
	&.badge-primary {
		background-color: $primary;
		color: #ffffff;
	}
	&.badge-danger {
		background-color: $danger;
		color: $white;
	}
	&.badge-alert {
		background-color: $alert;
		color: $white;
	}
	&.badge-gray {
		background-color: #cecece;
		color: $white;
	}
	&.badge-warning {
		background-color: $warning;
		color: $white;
	}
	&.badge-default {
		background-color: $gray-white;
	}
}

