//---------------------------------------------------------//
// Override Boostrap Variables
//---------------------------------------------------------//

// Colors

$gray-white:          #f6f6f6;
$gray-light:      #BEC1C6;
$gray-lighter:      #E1E2EA;
$gray:                #82858D;
$gray-dark:       #44474E;
$gray-darker:     #272932;


$black:         #000000;
$white:         #ffffff;


$primary:      #723AE8;
$secondary:       #5fd190;
$alert:           #ffd50a;
$danger:          #e52e5a;
$success:         #5fd190;


$theme-colors: () !default;
$theme-colors: map-merge(
  (
    "alert":    $alert
  ),
  $theme-colors
);


$body-bg:       $gray-white;
$body-color:        $gray-dark;

$text-muted:           $gray-light;

$border-color:      $gray-lighter;


// Grid - Breakpoints - Container

$container-xl: 1200px;  

$grid-gutter-width:           20px;

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: $container-xl + $grid-gutter-width
);



$spacer: 1rem !default;
$spacer-1: 0.3125rem; // 5px
$spacer-2: 0.625rem;  // 10px
$spacer-3: 0.9375rem;  // 15px
$spacer-4: 1.25rem;  // 20px
$spacer-5: 1.563rem; // 25px
$spacers: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$spacers: map-merge(
  (
    0: 0,
    1: $spacer-1,
    2: $spacer-2,
    3: $spacer-3,
    4: $spacer-4,
    5: $spacer-5
  ),
  $spacers
);


// Typography


$font-size-base:              0.75rem;
$font-size-lg:                0.75rem;
$font-size-xl:                1rem;
$line-height-base:            1.33;


$font-family-base:          'AvenirLTStd', sans-serif;
$font-family-secondary:     'Roboto', sans-serif;


$headings-font-weight:       normal;
$headings-font-family:       $font-family-base;
$headings-line-height:        1.2;


$hr-border-color:             $gray-lighter;
$hr-margin-y:                 1.5rem;


$lead-font-size:              1rem;

$display4-size:               3rem;

$display1-weight:             inherit;
$display2-weight:             inherit;
$display3-weight:             inherit;
$display4-weight:             inherit;

$display-line-height:         1.1;



// Components - Padding


$border-radius:               0.1875rem;
$border-radius-lg:            0.1875rem;
$border-radius-sm:            0.1875rem;


$input-btn-padding-y:         0.4375rem;
$input-btn-padding-x:         0.8125rem;


$input-btn-padding-y-lg:      0.625rem;
$input-btn-padding-x-lg:      0.8125rem;

$input-btn-padding-y-xl:      1.375rem;
$input-btn-padding-x-xl:      1.25rem;


$input-btn-line-height:       1.2;


$label-margin-bottom:       0.58rem;



// Buttons

$btn-font-weight:             bold;



// Forms

$form-group-margin-bottom:           8px;
$label-margin-bottom:                4px;

$input-color:                     $body-color;
$input-border-color:                $border-color;
$input-placeholder-color:           $body-color;
$input-bg:                          $white;
$input-font-weight:               bold;



// Dropdown

$dropdown-min-width:                160px;
$dropdown-border-radius:            0;
$dropdown-padding-y:                0.5rem;
$dropdown-spacer:                   0;
$dropdown-border-color:             $gray-lighter;

$dropdown-item-padding-y:           0.5rem;
$dropdown-item-padding-x:           1.5rem;



// Tooltips

$tooltip-bg:                        $gray-darker;
$tooltip-font-size:                 11px;
$tooltip-max-width:                 200px;
$tooltip-color:                     $white;
$tooltip-border-radius:             $border-radius;
$tooltip-opacity:                   .9;
$tooltip-padding-y:                 5px;
$tooltip-padding-x:                 10px;
$tooltip-margin:                    0;

$tooltip-arrow-width:                7px;
$tooltip-arrow-height:              4px;



// Alert

$alert-padding-y:                   19px;
$alert-padding-x:                   1.25rem;
$alert-margin-bottom:               1rem;
$alert-border-radius:               8px;

$alert-bg-level:                    -10;
$alert-border-level:                -9;
$alert-color-level:                 6;