//---------------------------------------------------------//
// Forms
//---------------------------------------------------------// 


label {
	font-size: 11px;
	font-weight: bold;
}



// Select

select {
  &.form-control {
    background-image: url('#{$icon-url}/icon-select-arrow.png');
    background-repeat: no-repeat;
    background-size: rem(9) rem(6);
    background-position: right rem(10) center;
    padding-right: rem(20);
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 0.01px;
    text-overflow: '';
    color: $input-placeholder-color;
    &:focus {
      color: $black;
    }
  }
}


// Search Box

.search-box {
	position: relative;
	//width: 130px;
	.form-control {
		padding-left: 25px;
	}
	.search-btn {
		position: absolute;
		top: 50%;
		left: 5px;
		transform: translateY(-50%);
		padding: 5px;
		color: #82858D;
		font-size: 11px;
		line-height: 1;
	}
}




// Checkbox / Radio

.radio, .checkbox {
    padding: 0px 0 0 0;
    margin-top: 0;
    .control {
        display: inline-block;
        vertical-align: middle;
        margin-right: 15px;
        margin-bottom: 10px;
        &:last-child {
            margin-right: 0;
            margin-bottom: 10px;
        }
    }
}


.control {
	position: relative;
	display: block;
	padding: 0;
	padding-left: 18px;
	cursor: pointer;
	margin-bottom: 12px;
	color: $body-color;
	-webkit-transition: color $easing-speed $easing;
	transition: color $easing-speed $easing;
	&:last-child {
		margin-bottom: 0;
	}
	&.checkbox-only {
		padding: 0;
		margin-bottom: 0;
		display: inline-block;
		vertical-align: middle;
		line-height: 1;
		input {
			z-index: 2;
		}
		.control-indicator {
			position: relative;
			top: rem(-1);
		}
	}
	input[type="checkbox"] {
		margin-left: 0;
	}
}

.control input {
	position: absolute;
	opacity: 0;
	z-index: -1; 
	cursor: pointer;
}
.control-indicator {
	position: absolute;
	left: 0;
	display: block;
	text-align: center;
	background-color: #ffffff;
}

.control-indicator, .control-label {
    pointer-events: none;
     -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.control-label {
	font-weight: normal;
}


/* Focus */
.control input:focus ~ .control-indicator {
  
}

/* Checked state */
.control input:checked ~ .control-indicator {
  	
}

/* Active */
.control input:active ~ .control-indicator {
  	
}

/* Checkbox modifiers */


.checkbox .control-indicator {
  	width: 12px;
  	height: 12px;
  	background-color: #ffffff;
  	border: 1px solid $gray-light;
  	line-height: 12px;
  	text-align: center;
  	border-radius: 2px;
  	top: 0px;
}
.checkbox input:checked ~ .control-indicator {
	background-color: $body-color;
	border-color: $body-color;
  	&:after {
  		content: '';
  		font-family: 'Fabric External MDL2 Assets';
  		color: #ffffff;
  		font-size: 9px;
  		position: relative;
  		top: -1px;
  	}
}

.control input:checked ~ .control-label {
	font-weight: bold;
}


/* Radio modifiers */
.radio .control-indicator {
  	width: 12px;
  	height: 12px;
  	background-color: #ffffff;
  	border-radius: 50%;
  	border: 1px solid $border-color;
  	top: 0px;
}
.radio input:checked ~ .control-indicator {
  	&:after {
  		content: '';
  		width: 6px;
  		height: 6px;
  		background-color: $gray-darker;
  		border-radius: 50%;
  		position: absolute;
  		top: 2px;
    	left: 2px;
  	}
}





// Calendar Input

.calendar-input {
    position: relative;
    .datetimepicker-trigger {
        position: absolute;
        top: 50%;
        right: $input-btn-padding-x;
        transform: translateY(-50%);
    }
    input {
        background-image: url('#{$icon-url}/icon-calendar.svg');
        background-repeat: no-repeat;
        background-position: 10px 50%;
        padding-left: 30px;
    }
}


.datepicker, .timepicker {
    .glyphicon {
        vertical-align: middle;
        position: relative;
        top: -1px;
    }
    .glyphicon-chevron-right {
        &:before {
        	content: '\f105';
        	font-family: "Font Awesome 5 Free";
        }
    }
    .glyphicon-chevron-left {
        &:before {
        	content: '\f104';
        	font-family: "Font Awesome 5 Free";
        }
    }

    .glyphicon-chevron-up {
        background: url('#{$icon-url}/icon-arrow-top-primary.png') 0 50% no-repeat;
        @include background-retina(12, 8);
    }
    .glyphicon-chevron-down {
        background: url('#{$icon-url}/icon-arrow-bottom-primary.png') 0 50% no-repeat;
        @include background-retina(12, 8);
    }
    .table-condensed>thead>tr>th, .table-condensed>tbody>tr>th, .table-condensed>tfoot>tr>th, .table-condensed>thead>tr>td, .table-condensed>tbody>tr>td, .table-condensed>tfoot>tr>td {
        padding: 5px;
    }
    .btn {
        min-width: 1px;
    }
    table td span.active {
    	background-color: $primary;
    }
}





// Image Upload

.image-upload {
    border: 2px dashed $border-color;
    padding: 10px;
    border-radius: 10px;
    height: 180px;
    position: relative;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 11px;
    color: $text-muted;
    margin-bottom: 10px;
    .image-upload-content {
        
    }
    p {
        margin-bottom: 10px;
    }
    small {
        font-size: 12px;
        display: block;
        line-height: 1;
    }
}

.file {
    position: relative;
    display: inline-block;
    cursor: pointer;
    cursor: pointer;
}
.file input {
    margin: 0;
    filter: alpha(opacity=0);
    opacity: 0;
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    z-index: -1;
}

.upload-filename {
    font-size: 11px;
    font-weight: bold;
    color: $text-muted;
    span {
        display: inline-block;
        vertical-align: middle;
        margin-right: 8px;
    }
}

.upload-close-btn {
    opacity: 0.3;
}
